/* Ticket type pills */
.ticket-type-copy {
  margin-bottom: 10px;
}

.ticket-type-eyebrow {
  display: block;
  font-family: var(--filter-font-display);
  font-size: var(--filter-text-xs);
  font-weight: var(--filter-weight-bold);
  letter-spacing: var(--filter-tracking-wider);
  text-transform: uppercase;
  color: var(--filter-text-accent);
  margin-bottom: 4px;
}

.ticket-type-hint {
  display: block;
  font-family: var(--filter-font-body);
  font-size: var(--filter-text-xs);
  font-weight: var(--filter-weight-normal);
  letter-spacing: var(--filter-tracking-normal);
  color: var(--filter-text-muted);
}

.ticket-type-pills {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.ticket-type-pill {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 6px 12px;

  /* Typography - Cormorant Garamond for consistency */
  font-family: var(--filter-font-body);
  font-size: var(--filter-text-sm);
  font-weight: var(--filter-weight-semibold);
  letter-spacing: var(--filter-tracking-wide);
  text-transform: uppercase;
  color: var(--filter-text-secondary);

  /* Styling */
  background: var(--filter-pill-bg);
  border: 1px solid var(--filter-pill-border);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.15s ease;
}

.ticket-type-pill input {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.ticket-type-pill:hover {
  color: var(--filter-text-primary);
  background: rgba(0, 143, 88, 0.15);
  border-color: rgba(0, 143, 88, 0.4);
}

.ticket-type-pill:has(input:checked) {
  color: var(--filter-text-primary);
  background: var(--filter-pill-active-bg);
  border-color: var(--filter-pill-active-border);
}

.ticket-type-pill--disabled {
  opacity: 0.45;
  cursor: not-allowed;
  border-style: dashed;
}

/* Filter card legacy styles */
.filter-card {
  padding: 12px 14px;
  margin: 0;
  background: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: none;
}

.filter-card:last-of-type,
.filter-card--actions {
  border-bottom: none;
}

.filter-card--actions {
  padding: 10px 14px;
  background: linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.15) 100%);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.filter-card--actions .th-filter-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  padding: 0;
  margin: 0;
  border: none;
}

/* Hide old meta strips */
.filter-meta-strip {
  display: none !important;
}

/* ==========================================================================
   RESPONSIVE
   ========================================================================== */

@media (max-width: 1200px) {
  .th-header-shell .th-filter-btn {
    right: 5px;
    padding: 2px;
  }

  .th-header-shell .th-filter-btn::before {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .th-header-shell .th-filter-btn {
    right: 4px;
    padding: 1px;
    opacity: 0.8;
    width: 26px;
    height: 26px;
  }

  .th-header-shell .th-filter-btn::before {
    font-size: 15px;
  }

  .th-filter-dropdown {
    min-width: 240px;
    max-width: 320px;
  }

  #filter-date {
    min-width: 340px;
    max-width: 400px;
  }

  .date-filter-grid--sleek {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 640px) {
  .th-filter-dropdown,
  .th-filter-dropdown.nav-panel-dropdown {
    position: fixed !important;
    top: auto !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    min-width: 100% !important;
    max-width: 100% !important;
    max-height: 65vh;
    border-radius: 16px 16px 0 0 !important;
    transform: none;
  }

  .th-filter-dropdown::before {
    display: none;
  }

  .th-filter-dropdown.open {
    animation: filterSlideUp 0.25s cubic-bezier(0.2, 0.8, 0.2, 1);
  }

  @keyframes filterSlideUp {
    from {
      opacity: 0;
      transform: translateY(100%);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  /* Mobile drag handle */
  .th-filter-dropdown::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 50%;
    transform: translateX(-50%);
    width: 32px;
    height: 4px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 2px;
  }

  .date-filter-grid--sleek {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .filter-grid--2col {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  /* Larger touch targets on mobile */
  .filter-pill {
    padding: 10px 14px;
    font-size: var(--filter-text-sm);
    min-height: 40px;
  }

  .filter-option {
    padding: 10px 12px;
    min-height: 44px;
  }

  .filter-option input[type="checkbox"] {
    width: 18px;
    height: 18px;
  }

  .filter-body {
    padding: 12px 16px;
    max-height: calc(65vh - 120px);
  }

  .filter-toolbar {
    padding: 12px 16px;
  }

  .filter-toolbar-title {
    font-size: var(--filter-text-sm);
  }

  .filter-actions,
  .filter-card--actions .th-filter-actions {
    padding: 12px 16px;
  }

  .th-filter-apply,
  .th-filter-reset {
    padding: 12px 20px;
    font-size: var(--filter-text-sm);
    min-height: 44px;
  }

  /* Date filter mobile adjustments */
  .date-filter-toolbar {
    padding: 12px 16px;
  }

  .date-filter-select-all-compact {
    padding: 8px 12px;
    font-size: var(--filter-text-sm);
  }

  .date-filter-categories {
    padding: 12px 16px;
  }
}

@media (max-width: 480px) {
  .th-header-shell .th-filter-btn {
    right: 2px;
    padding: 1px;
  }

  .th-header-shell .th-filter-btn::before {
    font-size: 14px;
  }
}

/* ==========================================================================
   ACCESSIBILITY
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  .th-filter-dropdown,
  .th-filter-dropdown.open,
  .th-header-shell .th-filter-btn,
  .filter-chip-btn,
  .filter-pill,
  .filter-option {
    transition: none;
    animation: none;
  }
}

@media (prefers-contrast: high) {
  .th-filter-dropdown {
    border-width: 2px;
    border-color: rgba(var(--color-primary-bright-rgb), 0.6);
  }

  .filter-pill:has(input:checked),
  .filter-option:has(input:checked) {
    border-width: 2px;
  }
}

