        0 1px 4px rgba(0, 0, 0, 1),
        0 0 2px rgba(0, 0, 0, 0.8); /* Enhanced glow with stronger contrast */
    opacity: 1;
}

/* Elegant underline animation */
.nav-link::after {
    content: none !important;
}

.nav-link::before {
    content: '';
    position: absolute;
    bottom: 3px;
    left: 0;
    right: 0;
    height: 1.5px; /* Thinner, more elegant */
    background: linear-gradient(
        90deg,
