/* Hover underline for non-active links */
.nav-link:not(.active):not([aria-current="page"]):hover::before {
    opacity: 1;
    transform: scaleX(1);
    background: linear-gradient(
        90deg,
        transparent,
        rgba(0, 255, 170, 0.9),
