/* ===== Back to Top Button - Sleek Minimalist Design ===== */
.back-to-top {
    position: fixed;
    bottom: 32px;
    right: 32px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: rgba(13, 27, 51, 0.6);
    border: 0.5px solid rgba(var(--color-primary-bright-rgb), 0.25);
    color: rgba(0, 255, 170, 0.7);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
    font-size: 0.75rem;
    font-weight: 300;
    cursor: pointer;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 
        0 1px 4px rgba(0, 0, 0, 0.4),
        0 0 0 0.5px rgba(0, 0, 0, 0.15) inset;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    transition: 
        opacity 0.25s ease,
        transform 0.25s cubic-bezier(0.2, 0.8, 0.2, 1),
        border-color 0.2s ease,
        color 0.2s ease,
        background 0.2s ease;
    opacity: 0;
    visibility: hidden;
    transform: translateY(15px);
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    outline: none;
}

.back-to-top.visible {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.back-to-top:hover {
    background: rgba(0, 143, 88, 0.12);
    border-color: rgba(0, 255, 170, 0.5);
    color: rgba(0, 255, 170, 0.95);
    box-shadow: 
        0 2px 8px rgba(0, 0, 0, 0.5),
        0 0 0 0.5px rgba(0, 0, 0, 0.15) inset,
        0 0 12px rgba(0, 255, 170, 0.15);
    transform: translateY(-1px);
}

.back-to-top:active {
    transform: translateY(0);
    background: rgba(0, 143, 88, 0.18);
}

/* ==========================================================================
   UNIFIED NAV STYLING - Bear Background Pages
   Consistent nav transitions across dashboard, weekly-lineup, odds-market
   ========================================================================== */

/* Unified nav positioning for all bear-background pages */
body.page-weekly-lineup .brand-nav,
body.page-odds-market .brand-nav,
body.page-active-picks .brand-nav {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    margin-top: 0 !important;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    padding-left: 0.75in !important;
    padding-right: 0.75in !important;
    z-index: 1000 !important;
}

body.page-weekly-lineup .nav-links,
body.page-odds-market .nav-links,
body.page-active-picks .nav-links {
    margin-top: 0;
}

body.page-weekly-lineup .nav-link,
body.page-odds-market .nav-link,
body.page-active-picks .nav-link {
    color: #e6f2f6 !important; /* Soft emerald-tinted white for readability */
    font-family: 'Cormorant Garamond', serif !important;
    font-weight: 600 !important;
    font-size: 0.75rem !important; /* Consistent font size */
    text-transform: uppercase !important;
    letter-spacing: 0.1em !important;
    text-shadow: 
        0 1px 2px rgba(0, 31, 63, 0.9); /* Removed green glow from default state */
    opacity: 0.85 !important; /* Slightly reduced opacity for non-active */
    padding: 4px 8px !important;
    -webkit-font-smoothing: antialiased;
    transition: color 0.3s ease, text-shadow 0.3s ease, background 0.3s ease, transform 0.2s ease;
}

body.page-weekly-lineup .nav-link:hover,
body.page-odds-market .nav-link:hover,
body.page-active-picks .nav-link:hover {
    color: #00e6a3; /* Brighter emerald on hover */
    text-shadow: 
        0 0 8px rgba(0, 214, 137, 0.5),
        0 1px 2px rgba(0, 31, 63, 0.9);
    background: transparent !important; /* Clean hover, no background */
    transform: translateY(-1px);
    opacity: 1 !important;
}

body.page-weekly-lineup .nav-link.active,
body.page-weekly-lineup .nav-link[aria-current="page"],
body.page-odds-market .nav-link.active,
body.page-odds-market .nav-link[aria-current="page"],
body.page-active-picks .nav-link.active,
body.page-active-picks .nav-link[aria-current="page"] {
    color: #00e6a3; /* Emerald for active */
    text-shadow: 
        0 0 6px rgba(0, 214, 137, 0.4),
        0 1px 2px rgba(0, 31, 63, 0.9);
    background: transparent !important; /* Only underline for active state */
    opacity: 1 !important;
}


/* ===== Responsive Adjustments ===== */
@media (max-width: 1200px) {
    .nav-links {
        gap: 3px; /* Maintain compact spacing */
    }
    
    .nav-link {
        font-size: 0.75rem;
        letter-spacing: 0.1em;
        padding: 4px 5px;
    }
}

@media (max-width: 768px) {
    .brand-nav {
        left: 0;
        right: 0;
        padding-inline: 16px;
    }
    
    .nav-links {
        gap: 2px; /* Very compact on mobile */
    }
    
    .nav-link {
        font-size: 0.68rem;
        letter-spacing: 0.08em;
        padding: 3px 4px;
    }
    
    .nav-dropdown-menu {
        width: 260px;
        right: -10px;
    }
    
    .back-to-top {
        bottom: 24px;
        right: 24px;
        width: 32px;
        height: 32px;
    }
    
}

/* ===== Manual Upload Section ===== */
.manual-upload-content {
    display: flex;
    flex-direction: column;
    gap: 6px;
    margin-top: 6px;
    padding-top: 6px;
    border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.upload-drop-zone {
    border: 1px dashed rgba(0, 214, 137, 0.3);
    border-radius: 4px;
    padding: 6px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 8px;
    background: linear-gradient(145deg, rgba(0, 0, 0, 0.3), rgba(0, 30, 20, 0.2));
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    position: relative;
    overflow: hidden;
    min-height: 32px;
}

.upload-drop-zone::before {
    content: '☁️';
    display: block;
    font-size: 0.9rem;
    margin: 0;
    opacity: 0.7;
    filter: grayscale(100%) brightness(1.5);
    transition: all 0.3s ease;
    line-height: 1;
}

.upload-drop-zone:hover,
.upload-drop-zone.drag-over {
    border-color: var(--color-primary-bright, #00d689);
    background: linear-gradient(145deg, rgba(0, 214, 137, 0.1), rgba(0, 30, 20, 0.3));
    box-shadow: 0 0 15px rgba(0, 214, 137, 0.1) inset;
}

.upload-drop-zone:hover::before,
.upload-drop-zone.drag-over::before {
    transform: scale(1.1);
    opacity: 1;
    filter: none;
}

.drop-zone-text {
    display: block;
    font-family: var(--sportsbook-heading-font);
    font-size: 0.65rem;
    color: #b0bec5;
    margin: 0;
    pointer-events: none;
    letter-spacing: 0.02em;
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left;
}

.hidden-file-input {
    display: none;
}

.upload-browse-btn {
    background: rgba(0, 214, 137, 0.1);
    border: 1px solid rgba(0, 214, 137, 0.3);
    color: var(--color-primary-bright, #00d689);
    font-family: var(--sportsbook-heading-font);
    font-size: 0.6rem;
    padding: 2px 8px;
    border-radius: 3px;
    cursor: pointer;
    transition: all 0.2s ease;
    text-transform: uppercase;
    letter-spacing: 0.05em;
