    margin-right: 8px;
  }

  /* Grid area assignments */
  .picks-table tbody tr td:nth-child(1) {
    grid-area: pick;
  }

  .picks-table tbody tr td:nth-child(2) {
    grid-area: league;
  }

  .picks-table tbody tr td:nth-child(3) {
    grid-area: teams;
  }

  .picks-table tbody tr td:nth-child(4) {
    grid-area: spread;
  }

  .picks-table tbody tr td:nth-child(5) {
    grid-area: status;
  }

  .picks-table tbody tr td:nth-child(6) {
    grid-area: money;
  }
}

/* ===== MODAL AND POPUP FIXES ===== */

.modal,
.modal-overlay {
  z-index: 10000;
}

.modal-content {
  max-height: 90vh;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

/* ===== SCROLLING OPTIMIZATION ===== */

.table-container {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  border: 1px solid rgba(0, 214, 137, 0.1);
  border-radius: 4px;
}

.table-container::-webkit-scrollbar {
  height: 4px;
}

.table-container::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
}

.table-container::-webkit-scrollbar-thumb {
  background: rgba(0, 214, 137, 0.4);
  border-radius: 2px;
}

/* ===== KEYBOARD SUPPORT ===== */

/* Visible focus states for keyboard navigation */
button:focus,
a:focus,
input:focus,
select:focus,
textarea:focus {
  outline: 2px solid rgba(0, 214, 137, 0.6);
  outline-offset: 2px;
}

/* ===== UTILITY CLASSES ===== */

.is-mobile {
  /* Applied to body when mobile UI is active */
}

.mobile-simplified-ui {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 12px;
  margin-bottom: 12px;
}

.mobile-refresh-btn {
  min-height: 44px;
  padding: 12px 16px;
  background: rgba(0, 214, 137, 0.12);
  border: 1px solid rgba(0, 214, 137, 0.3);
  color: rgba(0, 214, 137, 1);
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.2s ease;
}

.mobile-refresh-btn:active,
.mobile-refresh-btn:hover {
  background: rgba(0, 214, 137, 0.2);
  border-color: rgba(0, 214, 137, 0.6);
}
